class Overlay {
  constructor() {
    this.overlay = document.createElement('article');
    this.overlay.classList.add('loading');

    return this.overlay;
  }
}

class Svg {
  constructor() {
    this.svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
    this.svg.classList.add('loading__svg');

    this.rect = document.createElementNS('http://www.w3.org/2000/svg', 'rect');

    this.svg.append(this.rect);

    return this.svg;
  }
}

const timeSet = (s) => {
  return new Promise((resolve) => {
    setTimeout(resolve, Number(s) * 1000);
  });
};

const loading = {
  minWaitTime: 0.3,
  maxWaitTime: 3,
  isExcuting: false,

  on: async function (waitTime = null) {
    // ìž…ë ¥í•œ ì´ˆë§Œí¼ ê¸°ë‹¤ë¦° í›„ ë¡œë”© ì‹¤í–‰
    if (!!waitTime) {
      if (waitTime > this.maxWaitTime || typeof waitTime !== 'number') {
        throw new Error('loading.on í•¨ìˆ˜ì˜ ì¸ìžê°€ ìˆ«ìžê°€ ì•„ë‹ˆê±°ë‚˜ ìµœëŒ€ê°’ì„ ì´ˆê³¼í–ˆìŠµë‹ˆë‹¤.');
      }

      await timeSet(waitTime);
    }

    this.isExcuting = true;

    const DOMfragment = document.createDocumentFragment();

    const overlay = new Overlay();
    const svg = new Svg();

    overlay.append(svg);
    DOMfragment.append(overlay);

    document.body.append(DOMfragment);

    // ìµœëŒ€ ëŒ€ê¸°ì‹œê°„ í›„ì—ë„ ë¡œë”©ì´ êº¼ì§€ì§€ ì•Šì•˜ë‹¤ë©´ => ì¦‰, off() ì‹¤í–‰ìœ¼ë¡œ isExcutingì´ falseê°€ ë˜ì§€ ì•Šì•˜ë‹¤ë©´ ë¡œë”© ì‚­ì œ ìžë™ ì‹¤í–‰
    await timeSet(this.maxWaitTime);

    if (this.isExcuting === true) {
      console.warn('loading.off ê°€ í˜¸ì¶œë˜ì§€ ì•Šì•„ ë¡œë”© ì»´í¬ë„ŒíŠ¸ê°€ ìžë™ ì‚­ì œì²˜ë¦¬ ë˜ì—ˆìŠµë‹ˆë‹¤.');
      this.deleteLoading();
    }
  },

  off() {
    const recursiveTimer = (s = this.minWaitTime) => {
      setTimeout(() => {
        const deleteDone = this.deleteLoading();
        if (deleteDone) {
          this.waitTimeCnt = 0;
          return;
        } //
        else {
          return this.off();
        }
      }, s * 1000);
    };

    recursiveTimer(0.25); // ìž…ë ¥í•œ ì´ˆ ê°„ê²©ìœ¼ë¡œ ë¡œë”© ë„ëŠ” í•¨ìˆ˜ í˜¸ì¶œ
  },

  deleteLoading() {
    const loadingElem = document.querySelector('.loading');

    if (!!loadingElem) {
      loadingElem.remove();
      this.isExcuting = false;

      return true;
    } //
    else {
      return false;
    }
  },
};

export default loading;
